$general-font: 'Open Sans';

$min-width-md: 700px;
$min-width-lg: 900px;


$header-height-sm: 47px;
$header-height-md: 65px;
$header-height-lg: 70px;


/*  COLORS */
$color-green: rgba(61, 101, 86, .9);
$color-green-hover: rgba(36, 57, 49, .9);
$color-green-dark: rgba(49, 76, 66, 0.8);
$color-green-light: rgba(36, 171, 125, .9);
$color-green-light-hover: rgba(45, 148, 107, .9);
$color-white: #fff;
$color-white-alpha: rgba(255, 255, 255, .8);
$color-grey: #2b2b2b;
$color-disabled: rgba(116, 119, 118, 0.9);
$color-black-alpha: rgba(0, 0, 0, .2);
$color-red: #cd5f57;
$color-orange: #ffab56;
$color-blue: #64a5a9;

$color-black-bg: rgba(0, 0, 0, .8);

$color-invalid: rgba(255, 0, 11, .8);

$color-yellow: #fff17d;

$color-ava-green: #52be5d;
$color-ava-red: #ea5f56;
$color-ava-yellow: #eecf7e;

$color-red-state: #cc2128;
$color-green-state: #6cbb2d;






.change-avatar-page {
  .user-info {
    margin-top: 15px;
    margin-bottom: 6px;
    p {
      font-size: 14px;
    }
    h2 {
      font-weight: bold;
      font-size: 17px;
    }
  }
  .user-icon {
    float: left;
    margin-left: 5px;
  }
  .block-name {
    position: absolute;
    height: 100%;
    right: 0;
    left: 75px;
  }
  .tab-icon {
    position: relative;
    width: 44px;
    height: 44px;
    background-color: $color-black-alpha;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    border-radius: 22px;
    border: 1px solid $color-white;
    display: inline-block;
    margin: 10px;
    &.men {
      background-image: url(./men.png);
    }
    &.women {
      background-image: url(./women.png);
    }
    &.active {
      border-color: $color-yellow;
      &.men {
        background-image: url(./men_active.png);
      }
      &.women {
        background-image: url(./women_active.png);
      }
    }
  }
  .avatars {
    position: relative;
    width: 200%;
    text-align: center;
    .user-icon {
      float: none;
      display: inline-block;
      width: 56px;
      height: 56px;
      margin: 5px;
      &.touchHover {
        .avatar_bg {
          background-color: $color-green-light;
        }
      }
      &.active {
        .avatar_bg {
          background-color: $color-ava-green;
        }
      }

    }
    &[data-state="1"] {
      left: 0;
    }
    &[data-state="2"] {
      left: -100%;
    }
  }
  @media (min-width: $min-width-md) {
    .block-name {
      left: 110px;
      p {
        font-size: 18px;
      }
      h2 {
        font-size: 24px;
      }
    }
  }
}
.csstransforms .change-avatar-page {
  .avatars {
    left: 0;
    transition: transform 0.5s ease;
    &[data-state="1"] {
      transform: translate(0, 0);
    }
    &[data-state="2"] {
      transform: translate(-50%, 0);
    }
  }
}



