$general-font: 'Open Sans';

$min-width-md: 700px;
$min-width-lg: 900px;


$header-height-sm: 47px;
$header-height-md: 65px;
$header-height-lg: 70px;


/*  COLORS */
$color-green: rgba(61, 101, 86, .9);
$color-green-hover: rgba(36, 57, 49, .9);
$color-green-dark: rgba(49, 76, 66, 0.8);
$color-green-light: rgba(36, 171, 125, .9);
$color-green-light-hover: rgba(45, 148, 107, .9);
$color-white: #fff;
$color-white-alpha: rgba(255, 255, 255, .8);
$color-grey: #2b2b2b;
$color-disabled: rgba(116, 119, 118, 0.9);
$color-black-alpha: rgba(0, 0, 0, .2);
$color-red: #cd5f57;
$color-orange: #ffab56;
$color-blue: #64a5a9;

$color-black-bg: rgba(0, 0, 0, .8);

$color-invalid: rgba(255, 0, 11, .8);

$color-yellow: #fff17d;

$color-ava-green: #52be5d;
$color-ava-red: #ea5f56;
$color-ava-yellow: #eecf7e;

$color-red-state: #cc2128;
$color-green-state: #6cbb2d;






.blockTopics-page {
  .btn {
    line-height: 16px;
    height: auto;
    padding: 5px 51px 5px 30px;
    margin-top: 5px;
    text-align: left;
    .icon {
      position: absolute;
      width: 40px;
      height: 100%;
      top: 0;
      right: 0;
      background-position: center;
      background-size: 41px 41px;
      background-image: url(~publicImg/icons/unlock.png);
    }
    &.blocked {
      background-color: rgba(35, 62, 53, 0.8);
      border-color: $color-grey;
      .icon {
        background-image: url(~publicImg/icons/lock.png);
      }
    }
  }
  .table-middle-block {
    min-height: 30px;
  }


}



