$general-font: 'Open Sans';

$min-width-md: 700px;
$min-width-lg: 900px;


$header-height-sm: 47px;
$header-height-md: 65px;
$header-height-lg: 70px;


/*  COLORS */
$color-green: rgba(61, 101, 86, .9);
$color-green-hover: rgba(36, 57, 49, .9);
$color-green-dark: rgba(49, 76, 66, 0.8);
$color-green-light: rgba(36, 171, 125, .9);
$color-green-light-hover: rgba(45, 148, 107, .9);
$color-white: #fff;
$color-white-alpha: rgba(255, 255, 255, .8);
$color-grey: #2b2b2b;
$color-disabled: rgba(116, 119, 118, 0.9);
$color-black-alpha: rgba(0, 0, 0, .2);
$color-red: #cd5f57;
$color-orange: #ffab56;
$color-blue: #64a5a9;

$color-black-bg: rgba(0, 0, 0, .8);

$color-invalid: rgba(255, 0, 11, .8);

$color-yellow: #fff17d;

$color-ava-green: #52be5d;
$color-ava-red: #ea5f56;
$color-ava-yellow: #eecf7e;

$color-red-state: #cc2128;
$color-green-state: #6cbb2d;






.question-widget {
  //height: 470px;
  .question-container {
    background: $color-white-alpha;
    border-radius: 5px;
    padding: 10px;
    margin: 0 -5px;
    p {
      color: $color-grey;
      padding: 10px;
    }
  }
  .img_question {
    position: relative;
    width: 100%;
    height: 100px;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
  }
  .question-done {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .btn {
    margin-top: 10px;
    border-radius: 5px;
    height: auto;
    padding: 5px;
    line-height: 15px;
    font-size: 15px;
    .table-middle-block {
      min-height: 55px;
    }
    &.green-state-animate {
      transition: background-color 1s ease;
    }
    &.green-state, &.green-state-animate {
      background-color: $color-green-state;
    }
    &.red-state {
      background-color: $color-red-state;
    }
  }
  &.blockAnswer {
    .answer-button {
      display: inline-block;
      width: 50%;
      height: 120px;
      padding: 5px;
      overflow: hidden;
      .btn {
        height: 100%;
        margin: 0;
        padding-left: 5px;
        padding-right: 5px;
        font-size: 12px;
      }
    }
  }
  .timer {
    width: 100%;
    height: 4px;
    border-radius: 2px;
    background: $color-red-state;
    margin-top: 30px;
    margin-bottom: 10px;
    position: relative;
    .separator {
      position:absolute;
      top: 0;
      height: 100%;
      width: 1px;
      background: #777335;
      &:last-child {
        display: none;
      }
    }
    .timer-fill {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background-color: $color-green-state;
      border-radius: 2px;
    }
    .timer-label {
      width: 20px;
      height: 17px;
      position: absolute;
      background: url(./timer_label.png) center no-repeat;
      top: -21px;
      right: -12px;
    }
  }
  .timer-line {
    overflow: visible;
  }
  @media (min-width: $min-width-md) {
    .btn {
      font-size: 17px;
      padding-left: 5px;
      padding-right: 5px;
    }
    &.blockAnswer {
      .answer-button {
        .btn {
          font-size: 15px;
        }
      }
    }
  }
}



