$general-font: 'Open Sans';

$min-width-md: 700px;
$min-width-lg: 900px;


$header-height-sm: 47px;
$header-height-md: 65px;
$header-height-lg: 70px;


/*  COLORS */
$color-green: rgba(61, 101, 86, .9);
$color-green-hover: rgba(36, 57, 49, .9);
$color-green-dark: rgba(49, 76, 66, 0.8);
$color-green-light: rgba(36, 171, 125, .9);
$color-green-light-hover: rgba(45, 148, 107, .9);
$color-white: #fff;
$color-white-alpha: rgba(255, 255, 255, .8);
$color-grey: #2b2b2b;
$color-disabled: rgba(116, 119, 118, 0.9);
$color-black-alpha: rgba(0, 0, 0, .2);
$color-red: #cd5f57;
$color-orange: #ffab56;
$color-blue: #64a5a9;

$color-black-bg: rgba(0, 0, 0, .8);

$color-invalid: rgba(255, 0, 11, .8);

$color-yellow: #fff17d;

$color-ava-green: #52be5d;
$color-ava-red: #ea5f56;
$color-ava-yellow: #eecf7e;

$color-red-state: #cc2128;
$color-green-state: #6cbb2d;






li.user-widget {
  padding: 0;
  &:first-child {
    .user-label {
      border-top-left-radius: 21px;
      border-top-right-radius: 21px;
    }
  }
  &:last-child {
    .user-label {
      border-bottom-left-radius: 21px;
      border-bottom-right-radius: 21px;
    }
  }
  .user-label {
    position: relative;
    width: 100%;
    height: 46px;
    &.touchHover {
      background-color: $color-green-hover;
    }
    p {
      height: 46px;
      float: left;
      text-align: center;
      line-height: 43px;
      &.number {
        width: 22px;
        text-align: right;
        padding-right: 2px;
      }
    }
    .user-icon {
      float: left;
      width: 37px;
      height: 37px;
      margin-top: 5px;
    }
    .user-name {
      position: absolute;
      top: 0;
      left: 70px;
      right: 70px;
      text-align: left;
    }
    .score {
      float: right;
      font-weight: bold;
      text-align: right;
      display: block;
      width: 50px;
      margin-right: 30px;
      font-size: 13px;
      line-height: 44px;
    }
    .arrow {
      position: absolute;
      top: 0;
      height: 100%;
      right: 10px;
      width: 10px;
      background: url(~publicImg/button/arrow.png) no-repeat center;
      background-size: 6px 10px;
      transform-origin: 50% 50%;
      transform: rotate(90deg);
    }
  }
  &.open {
    .inside-block {
      display: block;
    }
    .user-label .arrow {
      transform: rotate(-90deg);
    }
  }
  .inside-block {
    display: none;
    position: relative;
    width: 100%;
    height: auto;
    overflow: hidden;
    background-color: $color-green-hover;
    .icon {
      margin: 11px auto 0 auto;
      width: 33px;
      height: 33px;
      border-radius: 50%;
      border: 1px solid white;
      background-position: center;
      background-repeat: no-repeat;
      &.games {
        background-image: url(./games.png);
        background-size: 15px 16px;
      }
      &.dones {
        background-image: url(./dones.png);
        background-size: 18px 18px;
        border-color: $color-ava-green;
      }
      &.draws {
        background-image: url(./draws.png);
        background-size: 20px 19px;
        border-color: $color-ava-yellow;
      }
      &.fails {
        background-image: url(./fails.png);
        background-size: 18px 18px;
        border-color: $color-ava-red;
      }
    }
    .icon-block {
      float: left;
      width: 25%;
      height: 95px;
      text-align: center;
      span {
        font-size: 10px;
      }
      h2 {
        font-size: 19px;
        margin-top: 7px;
      }
    }
    .buttons-block {
      float: left;
      width: 100%;
      text-align: center;
      padding-bottom: 10px;
      .btn {
        display: inline-block;
        width: 115px;
        margin: 0 7px;
        &.size-big {
          margin-top: 10px;
          width: 245px;
        }
      }
    }
  }
  &.no-number {
    .user-label {
      .user-name {
        left: 60px;
      }
      p.number {
        width: 11px;
      }
    }
  }
  &.no-rating {
    .user-label {
      .user-name {
        right: 40px;
      }
    }
  }
  @media (min-width: $min-width-md) {
    &:first-child {
      .user-label {
        border-top-left-radius: 30px;
        border-top-right-radius: 30px;
      }
    }
    &:last-child {
      .user-label {
        border-bottom-left-radius: 30px;
        border-bottom-right-radius: 30px;
      }
    }
    .user-label {
      height: 60px;
      .user-icon {
        width: 50px;
        height: 50px;
        margin-top: 4px;
      }
      .user-name {
        left: 100px;
        right: 90px;
      }
      .score {
        width: 60px;
        margin-right: 35px;
        font-size: 16px;
        line-height: 60px;
      }
      .arrow {
        right: 13px;
      }
      p {
        height: 60px;
        line-height: 60px;
        font-size: 18px;
        &.number {
          width: 35px;
          padding-right: 5px;
        }
      }
    }
    .inside-block {
      .icon-block {
        height: 135px;
        span {
          font-size: 13px;
        }
        h2 {
          font-size: 22px;
          margin-top: 12px;
        }
      }
      .icon {
        width: 50px;
        height: 50px;
        &.games {
          background-size: 22px 23px;
        }
        &.dones {
          background-size: 25px 25px;
        }
        &.draws {
          background-size: 27px 26px;
        }
        &.fails {
          background-size: 25px 25px;
        }
      }
    }
  }
}



