$general-font: 'Open Sans';

$min-width-md: 700px;
$min-width-lg: 900px;


$header-height-sm: 47px;
$header-height-md: 65px;
$header-height-lg: 70px;


/*  COLORS */
$color-green: rgba(61, 101, 86, .9);
$color-green-hover: rgba(36, 57, 49, .9);
$color-green-dark: rgba(49, 76, 66, 0.8);
$color-green-light: rgba(36, 171, 125, .9);
$color-green-light-hover: rgba(45, 148, 107, .9);
$color-white: #fff;
$color-white-alpha: rgba(255, 255, 255, .8);
$color-grey: #2b2b2b;
$color-disabled: rgba(116, 119, 118, 0.9);
$color-black-alpha: rgba(0, 0, 0, .2);
$color-red: #cd5f57;
$color-orange: #ffab56;
$color-blue: #64a5a9;

$color-black-bg: rgba(0, 0, 0, .8);

$color-invalid: rgba(255, 0, 11, .8);

$color-yellow: #fff17d;

$color-ava-green: #52be5d;
$color-ava-red: #ea5f56;
$color-ava-yellow: #eecf7e;

$color-red-state: #cc2128;
$color-green-state: #6cbb2d;






#header-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: $header-height-sm;
  background: #3d3d47;
  overflow: hidden;
  @media (min-width: $min-width-md) {
    height: $header-height-md;
  }
  @media (min-width: $min-width-lg) {
    height: $header-height-lg;
  }
  .content {
    position: relative;
    width: 100%;
    height: 100%;
  }
  .icons-left {
    position: relative;
    float: left;
    height: 100%;
    overflow: hidden;
    padding-left: 15px;
  }
  .icon {
    position: relative;
    float: left;
    width: 40px;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center;
    &.back {
      background-image: url(./back.png);
      background-size: 24px 24px;
      &.touchHover {
        background-image: url(./back_hover.png);
      }
    }
    &.settings {
      background-image: url(./settings.png);
      background-size: 34px 18px;
      &.touchHover {
        background-image: url(./settings_hover.png);
      }
    }
    &.statistics {
      background-image: url(./statistics.png);
      background-size: 24px 24px;
      &.touchHover {
        background-image: url(./statistics_hover.png);
      }
    }
    &.chat {
      background-image: url(./chat.png);
      background-size: 25px 18px;
      &.touchHover {
        background-image: url(./chat_hover.png);
      }
    }
    .counter {
      position: absolute;
      display: inline-block;
      height: 13px;
      width: auto;
      padding: 0 4px;
      border-radius: 6px;
      background: $color-red;
      color: $color-white;
      text-align: center;
      top: 5px;
      right: 0px;
      font-size: 8px;
      line-height: 13px;
    }
    @media (min-width: $min-width-md) {
      width: 80px;
      .counter {
        height: 20px;
        padding: 0 6px;
        border-radius: 10px;
        font-size: 12px;
        line-height: 20px;
      }
      &.back {
        background-size: 41px 41px;
      }
      &.settings {
        background-size: 58px 30px;
      }
      &.statistics {
        background-size: 41px 41px;
      }
      &.chat {
        background-size: 45px 36px;
      }
    }
    @media (min-width: $min-width-lg) {

    }
  }
  .not-auth-label {
    position: absolute;
    top: 5px;
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
    display: none;
    color: #909090;
    p {
      line-height: 18px;
    }
    @media (min-width: $min-width-md) {
      top: 8px;
      p {
        line-height: 25px;
        font-size: 20px;
      }

    }
  }
  &.notAuth {
    .not-auth-label {
      display: block;
    }
    .settings, .statistics, .chat {
      display: none;
    }
  }
}



