.btn {
  height: 43px;
  line-height: 38px;
  text-align: center;
  background-color: $color-green;
  border: 1px solid $color-white-alpha;
  color: $color-white;
  border-radius: 21px;
  font-size: 15px;
  transition: background-color 0.3s ease;
  font-family: $general-font;
  &.touchHover {
    background-color: $color-green-hover;
    //border-color: $color-green-hover;
  }
  &:focus {
    outline: none;
  }
  &.size-small {
    height: 30px;
    font-size: 13px;
    line-height: 27px;
  }
  &.color-dark {
    background-color: $color-green-dark;
    &.touchHover {
      background-color: $color-green-hover;
    }
  }
  &.color-light {
    background-color: $color-green-light;
    &.touchHover {
      background-color: $color-green-light-hover;
    }
  }
  &.color-orange {
    background-color: $color-black-alpha;
    border-color: $color-orange;
    &.touchHover {
      background-color: $color-orange;
    }
  }
  &.color-yellow {
    background-color: $color-black-alpha;
    border-color: $color-yellow;
    &.touchHover {
      background-color: $color-yellow;
    }
  }
  &.color-red {
    background-color: $color-black-alpha;
    border-color: $color-red;
    &.touchHover {
      background-color: $color-red;
    }
  }
  &.color-blue {
    background-color: $color-black-alpha;
    border-color: $color-blue;
    &.touchHover {
      background-color: $color-blue;
    }
  }
  &.single {
    margin-top: 15px;
    margin-bottom: 10px;
  }
  .button-icon {
    position: absolute;
    width: 32px;
    height: 32px;
    border: 1px solid $color-white;
    border-radius: 50%;
    top: 50%;
    margin-top: -16px;
    background-size: 100% 100%;
    left:5px;
    &.icon-settings { background-image: url(~publicImg/icons/settings/settings.png); }
    &.icon-change-avatar { background-image: url(~publicImg/icons/settings/change_avatar.png); }
    &.icon-block-subject { background-image: url(~publicImg/icons/settings/block_subject.png); }
    &.icon-change-themes { background-image: url(~publicImg/icons/settings/change_themes.png); }
    &.icon-user-blacklist { background-image: url(~publicImg/icons/settings/user_blacklist.png); }
    &.icon-buy-pro { background-image: url(~publicImg/icons/settings/buy_pro.png); }

    &.icon-facebook { background-image: url(~publicImg/icons/new_game/facebook_icon.png); }
    &.icon-random { background-image: url(~publicImg/icons/new_game/random_icon.png); }
    &.icon-search { background-image: url(~publicImg/icons/new_game/search_icon.png); }
    &.icon-vk-icon { background-image: url(~publicImg/icons/new_game/vk_icon.png); }

    &.icon-players-rating { background-image: url(~publicImg/icons/statistics/players_rating.png); }
    &.icon-rating-neighborhood { background-image: url(~publicImg/icons/statistics/raiting_neighborhood.png); }

    &.icon-more-info { background-image: url(~publicImg/icons/player_card/more_info_icon.png); }
    &.icon-chat-icon { background-image: url(~publicImg/icons/player_card/chat_icon.png); }
    &.icon-add-friend { background-image: url(~publicImg/icons/player_card/add_friend_icon.png); }
    &.icon-add-blacklist { background-image: url(~publicImg/icons/player_card/add_blacklist_player.png); }

    &.icon-settings { background-image: url(~publicImg/icons/settings/settings.png); }
    &.icon-settings { background-image: url(~publicImg/icons/settings/settings.png); }
    &.icon-settings { background-image: url(~publicImg/icons/settings/settings.png); }
    &.icon-settings { background-image: url(~publicImg/icons/settings/settings.png); }
  }
  .btn-arrow {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 20px;
    background: url(~publicImg/button/arrow.png) no-repeat left center;
    background-size: 6px 10px;
  }
  .counter {
    position: absolute;
    display: inline-block;
    height: 20px;
    width: auto;
    padding: 0 8px;
    border-radius: 9px;
    background: $color-red;
    color: $color-white;
    text-align: center;
    border: 2px solid $color-white;
    top: 50%;
    margin-top: -10px;
    right: 10px;
    font-size: 12px;
    line-height: 16px;
  }
  &.disabled {
    background-color: rgba(38, 48, 42, 0.7);
    color: #505853;
    border-color: #111614;
  }
  @media (min-width: $min-width-md) {
    height: 60px;
    line-height: 55px;
    border-radius: 30px;
    font-size: 21px;
    &.size-small {
      height: 38px;
      font-size: 15px;
      line-height: 35px;
    }
    .button-icon {
      width: 50px;
      height: 50px;
      border-width: 2px;
      margin-top: -25px;
    }
    .counter {
      height: 28px;
      padding: 0 12px;
      border-radius: 16px;
      margin-top: -14px;
      right: 14px;
      font-size: 18px;
      line-height: 22px;
    }
  }
}
.btn-set {
  line-height: 43px;
  overflow: hidden;
  & > .btn {
    border-right-width: 1px;
    border-radius: 0;
    &:first-child {
      border-top-left-radius: 21px;
      border-bottom-left-radius: 21px;
    }
    &:last-child {
      border-top-right-radius: 21px;
      border-bottom-right-radius: 21px;
    }
    @media (min-width: $min-width-md) {
      &:first-child {
        border-top-left-radius: 30px;
        border-bottom-left-radius: 30px;
      }
      &:last-child {
        border-top-right-radius: 30px;
        border-bottom-right-radius: 30px;
      }
    }
  }
}

label.input {
  padding: 0;
  span {
    display: block;
    text-align: center;
    margin-bottom: 2px;
  }
  input{
    background-color: $color-white;
    border: 1px solid $color-black-alpha;
    border-radius: 21px;
    display: block;
    height: 38px;
    width: 100%;
    color: $color-grey;
    text-indent: 15px;
    font-size: 15px;
    font-family: $general-font;
    transition: background-color 0.3s ease;
    &:focus {
      outline: none;
      border: 1px solid $color-grey;
    }
    &.invalid {
      border-color: $color-invalid;
      box-shadow: 0 0 3px $color-invalid;
      color: $color-invalid;
    }
  }
  @media (min-width: $min-width-md) {
    span {
      font-size: 18px;
      margin-bottom: 8px;
    }
    input {
      height: 60px;
      line-height: 55px;
      border-radius: 30px;
      font-size: 21px;
      text-indent: 30px;
    }
  }
}

.user-icon {
  position: relative;
  width: 46px;
  height: 46px;
  .avatar_bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
  .avatar {
    position: relative;
    width: 100%;
    height: 100%;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
  }
  &[data-user-icon-status="0"] {
    .avatar_bg {
      background-color: $color-black-alpha;
    }
  }
  &[data-user-icon-status="1"] {
    .avatar_bg {
      background-color: $color-ava-green;
    }
  }
  &[data-user-icon-status="2"] {
    .avatar_bg {
      background-color: $color-ava-red;
    }
  }
  &[data-user-icon-status="3"] {
    .avatar_bg {
      background-color: $color-ava-yellow;
    }
  }
  @for $i from 0 through 50 {
    &[data-user-icon-id="#{$i}"] {
      .avatar {
        background-image: url('~publicImg/avatars/#{$i}.png');
      }
    }
  }
  @for $i from 101 through 150 {
    &[data-user-icon-id="#{$i}"] {
      .avatar {
        background-image: url('~publicImg/avatars/#{$i}.png');
      }
    }
  }
  @media (min-width: $min-width-md) {
    width: 70px;
    height: 70px;
  }
}


