.game-page {
  .main-block {
    position: relative;
  }
  .half-block {
    position: relative;
    width: 50%;
    float: left;
    h2 {
      text-align: center;
      font-size: 16px;
      padding: 5px 10px 0 10px;
      line-height: 20px;
    }
  }
  .user-icon {
    display: block;
    width: 75px;
    height: 75px;
    margin: 0 auto;
  }
  .score-block {
    position: absolute;
    top: 27px;
    left: 50%;
    margin-left: -40px;
    width: 80px;
    text-align: center;
    font-size: 21px;
  }

  .list-container {
    padding: 15px;
    height: 350px;
  }


