$general-font: 'Open Sans';

$min-width-md: 700px;
$min-width-lg: 900px;


$header-height-sm: 47px;
$header-height-md: 65px;
$header-height-lg: 70px;


/*  COLORS */
$color-green: rgba(61, 101, 86, .9);
$color-green-hover: rgba(36, 57, 49, .9);
$color-green-dark: rgba(49, 76, 66, 0.8);
$color-green-light: rgba(36, 171, 125, .9);
$color-green-light-hover: rgba(45, 148, 107, .9);
$color-white: #fff;
$color-white-alpha: rgba(255, 255, 255, .8);
$color-grey: #2b2b2b;
$color-disabled: rgba(116, 119, 118, 0.9);
$color-black-alpha: rgba(0, 0, 0, .2);
$color-red: #cd5f57;
$color-orange: #ffab56;
$color-blue: #64a5a9;

$color-black-bg: rgba(0, 0, 0, .8);

$color-invalid: rgba(255, 0, 11, .8);

$color-yellow: #fff17d;

$color-ava-green: #52be5d;
$color-ava-red: #ea5f56;
$color-ava-yellow: #eecf7e;

$color-red-state: #cc2128;
$color-green-state: #6cbb2d;






.round-widget {
  height: 52px;

  .round{
    padding-bottom: 1px;
    padding-top: 1px;
    border-top:  1px solid black;
    margin-left: 2px;
    margin-right: 2px;
  }

  .round-col-l{
    padding-left: 0px;
    margin-left: 0px;
    margin-right: 0px;
  }
  .round-col-r{
    margin-left: 0px;
    padding-right: 0px;
    margin-right: 0px;
  }

  .line-height{
    vertical-align: middle;
    margin-left: 3px;
  }
  .round-center{
    text-align: center;
    vertical-align: middle;
  }

  .icon-container {
    text-align: center;
    float: left;
    width: 80px;
    margin: 5px 0;
    margin-left: 10px;
  }

  .text-container {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 60px;
    right: 60px;
  }
  .text-container p {
    font-size: 13px;
  }

  .time-container {
    text-align: center;
    vertical-align: middle;
    float: right;
    width: 80px;
    margin: 5px 0;
    margin-right: 10px;
  }

  .ui{
    list-style: none;
  }
  .game-round-name-container{
    padding-top: 5px;
    font-size: 10px;
    text-align: center;
    margin: 0 auto;
  }
  .game-round-center{
    padding-top: 23px;
    bottom: 0;
    text-align: center;
    margin: 0 auto;
    font-size: 13px;
  }
  .icon-score{
    display: inline-block;
    width: 23px;
  }
}



