$general-font: 'Open Sans';

$min-width-md: 700px;
$min-width-lg: 900px;


$header-height-sm: 47px;
$header-height-md: 65px;
$header-height-lg: 70px;


/*  COLORS */
$color-green: rgba(61, 101, 86, .9);
$color-green-hover: rgba(36, 57, 49, .9);
$color-green-dark: rgba(49, 76, 66, 0.8);
$color-green-light: rgba(36, 171, 125, .9);
$color-green-light-hover: rgba(45, 148, 107, .9);
$color-white: #fff;
$color-white-alpha: rgba(255, 255, 255, .8);
$color-grey: #2b2b2b;
$color-disabled: rgba(116, 119, 118, 0.9);
$color-black-alpha: rgba(0, 0, 0, .2);
$color-red: #cd5f57;
$color-orange: #ffab56;
$color-blue: #64a5a9;

$color-black-bg: rgba(0, 0, 0, .8);

$color-invalid: rgba(255, 0, 11, .8);

$color-yellow: #fff17d;

$color-ava-green: #52be5d;
$color-ava-red: #ea5f56;
$color-ava-yellow: #eecf7e;

$color-red-state: #cc2128;
$color-green-state: #6cbb2d;






.profile-page {
  .user-icon {
    display: inline-block;
    width: 55px;
    height: 55px;
  }
  .user-name {
    font-size: 19px;
    line-height: 28px;
    margin-top: -6px;
  }
  .user-info {
    padding: 10px 20px 15px 20px;
  }
  .icon {
    margin: 2px auto 0 auto;
    width: 42px;
    height: 42px;
    border-radius: 50%;
    border: 1px solid white;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    &.all-games {
      background-image: url(~publicImg/icons/player_card/games_icon.png);
    }
    &.dones {
      background-image: url(~publicImg/icons/player_card/win_icon.png);
      border-color: $color-ava-green;
    }
    &.rate {
      background-image: url(~publicImg/icons/player_card/score_icon.png);
      border-color: $color-ava-yellow;
    }
    &.number-rate {
      background-image: url(~publicImg/icons/player_card/position_icon.png);
      border-color: $color-ava-yellow;
    }
  }
  .icon-block {
    float: left;
    width: 50%;
    height: 84px;
    text-align: center;
    span {
      font-size: 10px;
      display: block;
    }
    h2 {
      font-size: 19px;
      margin-top: 4px;
    }
  }
  @media (min-width: $min-width-md) {
    .user-icon {
      width: 70px;
      height: 70px;
    }
    .user-name {
      font-size: 25px;
    }
    .icon-block {
      width: 25%;
      height: 120px;
      span {
        font-size: 13px;
        margin-top: 5px;
      }
      h2 {
        font-size: 21px;
        margin-top: 7px;
      }
    }
    .user-info {
      padding: 20px 10px 30px 10px;
    }
    .icon {
      width: 60px;
      height: 60px;
    }
  }

}



