$general-font: 'Open Sans';

$min-width-md: 700px;
$min-width-lg: 900px;


$header-height-sm: 47px;
$header-height-md: 65px;
$header-height-lg: 70px;


/*  COLORS */
$color-green: rgba(61, 101, 86, .9);
$color-green-hover: rgba(36, 57, 49, .9);
$color-green-dark: rgba(49, 76, 66, 0.8);
$color-green-light: rgba(36, 171, 125, .9);
$color-green-light-hover: rgba(45, 148, 107, .9);
$color-white: #fff;
$color-white-alpha: rgba(255, 255, 255, .8);
$color-grey: #2b2b2b;
$color-disabled: rgba(116, 119, 118, 0.9);
$color-black-alpha: rgba(0, 0, 0, .2);
$color-red: #cd5f57;
$color-orange: #ffab56;
$color-blue: #64a5a9;

$color-black-bg: rgba(0, 0, 0, .8);

$color-invalid: rgba(255, 0, 11, .8);

$color-yellow: #fff17d;

$color-ava-green: #52be5d;
$color-ava-red: #ea5f56;
$color-ava-yellow: #eecf7e;

$color-red-state: #cc2128;
$color-green-state: #6cbb2d;






.index-page {
  .user-info {
    margin-top: 15px;
    margin-bottom: 6px;
    p {
      font-size: 14px;
    }
    h2 {
      font-weight: bold;
      font-size: 17px;
    }
  }
  .user-icon {
    float: left;
    margin-left: 5px;
  }
  .block-name {
    position: absolute;
    height: 100%;
    right: 0;
    left: 75px;
  }
  .tips-header {
    margin-top: 10px;
    margin-bottom: 4px;
  }
  .tips-block {
    height: 102px;
    padding: 0 5px;

  }
  .game-container {
    margin-top: 15px;
    margin-bottom: 15px;
  }
  .game-item {
    color: $color-white;
    .user-icon {
      float: left;
      width: 40px;
      height: 40px;
      margin: 10px 0;
    }
    .text-container {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 55px;
      right: 60px;
      h2 {
        margin-top: 2px;
        margin-bottom: 6px;
        font-size: 15px;
      }
      p {
        font-size: 12px;
      }
    }
    .time-container {
      float: right;
      width: 40px;
      height: 40px;
      margin: 10px 0;
      font-size: 10px;
    }
  }
  .right-text {
    position: absolute;
    display: block;
    top: -6px;
    right: 0;
    cursor: pointer;
    width: 30px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    font-size: 20px;
  }

  .icon {
    display: block;
    width: 40px;
    height: 40px;
  }
  @media (min-width: $min-width-md) {
    .right-text {
      font-size: 30px;
    }
    .game-item {
      .user-icon {
        width: 55px;
        height: 55px;
      }
      .text-container {
        left: 85px;
        right: 80px;
        p {
          font-size: 15px;
        }
        h2 {
          font-size: 21px;
        }
      }
      .time-container {
        width: 45px;
        height: 55px;
        font-size: 13px;
      }
    }
  }

}



