$general-font: 'Open Sans';

$min-width-md: 700px;
$min-width-lg: 900px;


$header-height-sm: 47px;
$header-height-md: 65px;
$header-height-lg: 70px;


/*  COLORS */
$color-green: rgba(61, 101, 86, .9);
$color-green-hover: rgba(36, 57, 49, .9);
$color-green-dark: rgba(49, 76, 66, 0.8);
$color-green-light: rgba(36, 171, 125, .9);
$color-green-light-hover: rgba(45, 148, 107, .9);
$color-white: #fff;
$color-white-alpha: rgba(255, 255, 255, .8);
$color-grey: #2b2b2b;
$color-disabled: rgba(116, 119, 118, 0.9);
$color-black-alpha: rgba(0, 0, 0, .2);
$color-red: #cd5f57;
$color-orange: #ffab56;
$color-blue: #64a5a9;

$color-black-bg: rgba(0, 0, 0, .8);

$color-invalid: rgba(255, 0, 11, .8);

$color-yellow: #fff17d;

$color-ava-green: #52be5d;
$color-ava-red: #ea5f56;
$color-ava-yellow: #eecf7e;

$color-red-state: #cc2128;
$color-green-state: #6cbb2d;






.game-result-page {
  .main-block {
    position: relative;
  }
  .half-block {
    position: relative;
    width: 50%;
    float: left;
    h2 {
      text-align: center;
      font-size: 16px;
      padding: 5px 10px 0 10px;
    }
  }
  .user-icon {
    display: block;
    width: 75px;
    height: 75px;
    margin: 0 auto;
  }
  .score-block {
    position: absolute;
    top: 67px;
    left: 50%;
    margin-left: -40px;
    width: 80px;
    text-align: center;
    span {
      font-size: 41px;
      font-weight: bold;
      line-height: 20px;
    }
  }
  .score-header {
    margin-top: 40px;
    .text-min {
      font-size: 12px;
      margin-bottom: 5px;
    }
  }
  .slider {
    position: relative;
    width: 90%;
    height: 28px;
    border: 1px solid white;
    margin: 18px auto 0 auto;
    border-radius: 20px;
    background: $color-black-alpha;
    .separator {
      position: absolute;
      left: 50%;
      top: 0;
      width: 1px;
      height: 26px;
      background: white;
    }
    .icon {
      position: absolute;
      top: 0;
      right: 0;
      border-left: 1px solid white;
      width: 28px;
      height: 26px;
      background-size: 16px 16px;
      background-repeat: no-repeat;
      background-position: center;
      &:first-child {
        right: auto;
        left: 0;
        border-right: 1px solid white;
        border-left: none;
      }
      &.score-fail {
        background-image: url(score-fail.png);
      }
      &.score-done {
        background-image: url(score-done.png);
      }
    }
  }
  .my-slider-container {
    position: absolute;
    top: 0;
    height: 100%;
    right: 50%;
    left: 28px;
  }
  .opponent-slider-container {
    position: absolute;
    top: 0;
    height: 100%;
    left: 50%;
    right: 28px;
  }
  .text-block {
    position: relative;
    width: 90%;
    margin: 3px auto;
    font-size: 11px;
    span {
      position: absolute;
      display: inline-block;
      top: 0;
      width: 100%;
      padding: 0 23px;
      text-align: center;
      &:first-child {
        text-align: left;
      }
      &:last-child {
        text-align: right;
      }
    }
  }
  .my-slider, .opponent-slider {
    position: absolute;
    width: 0;
    height: 100%;
    right: 0;
    transition: width 2s ease;
    &.color-red {
      background: $color-ava-red;
    }
    &.color-green {
      background: $color-ava-green;
    }
  }
  .opponent-slider {
    right: auto;
    left: 0;
  }
  .user-status {
    text-align: center;
    padding-bottom: 20px;
    font-weight: bold;
  }
  .buttons-block {
    width: 90%;
    margin: 60px auto 0 auto;
    text-align: center;
    .btn {
      width: 45%;
      display: inline-block;
      &:first-child {
        margin-right: 25px;
      }
    }
  }
}



