$general-font: 'Open Sans';

$min-width-md: 700px;
$min-width-lg: 900px;


$header-height-sm: 47px;
$header-height-md: 65px;
$header-height-lg: 70px;


/*  COLORS */
$color-green: rgba(61, 101, 86, .9);
$color-green-hover: rgba(36, 57, 49, .9);
$color-green-dark: rgba(49, 76, 66, 0.8);
$color-green-light: rgba(36, 171, 125, .9);
$color-green-light-hover: rgba(45, 148, 107, .9);
$color-white: #fff;
$color-white-alpha: rgba(255, 255, 255, .8);
$color-grey: #2b2b2b;
$color-disabled: rgba(116, 119, 118, 0.9);
$color-black-alpha: rgba(0, 0, 0, .2);
$color-red: #cd5f57;
$color-orange: #ffab56;
$color-blue: #64a5a9;

$color-black-bg: rgba(0, 0, 0, .8);

$color-invalid: rgba(255, 0, 11, .8);

$color-yellow: #fff17d;

$color-ava-green: #52be5d;
$color-ava-red: #ea5f56;
$color-ava-yellow: #eecf7e;

$color-red-state: #cc2128;
$color-green-state: #6cbb2d;






.chatWidget {
    width:100%;
    height: 100%;
    display:table;

    .messages {
        display:table-row;
        height: 100%;
        padding-top: 39px;

        .message-collection {
            height:100%;
            overflow:auto;
            padding-right:20px;
            padding-left:20px;

            li {
                display: flex;

                align-items: flex-end;

                margin-bottom: 4px;

                &.left {
                    flex-direction: row;
                }
                &.right {
                    flex-direction: row-reverse;
                }

                &:first-child { margin-top: 39px; }

                .avatar {
                    img {
                        max-width: 26px;
                    }
                }

                .msg {
                    max-width: 66%;
                    border-style: solid;

                    margin:-1px 17.5px -6.5px;

                    .name {
                        margin: -5px -4px 0;

                        font-size: 6.66px;

                        &.self {
                            color: #bbbb83;
                        }
                    }

                    .text {
                        margin: 3px 5px -8px;

                        font-size: 8px;
                        line-height: 1.17;
                        color: #373737;

                        white-space: normal;
                        text-align: left;
                    }
                }

                &.left .msg {
                    border-width: 8px 12px 19px 18px;
                    border-image: url('img/msg-bg-grey.png') 16 24 38 36 fill stretch;
                }

                &.right .msg {
                    border-width: 8px 18px 19px 12px;
                    border-image: url('img/msg-bg-green.png') 16 36 38 24 fill stretch;
                    text-align: right;
                }
            }
        }
    }

    .editor {
        display:table-row;
        height: 0;

        border:1px solid #909191;
        background-color:#e8e8e8;

        .input-group {
            display: flex;
            padding:11px 20px 7px;

            .form-control { 
                height: 25px; 
                border: 1px solid #c3c3c3;
                border-radius: 4px;
                flex: 1;
            }

            .send {
                height: 25px; 
                width: 25px;
                border:none;
                padding:0;
                background: url('img/btn-send.png') no-repeat center;
                background-size: 23px 23px;

                margin-left:16px;

                &.touchHover {
                    background-image: url('img/btn-send-pressed.png');
                }
            }
        }
    }
}


