$general-font: 'Open Sans';

$min-width-md: 700px;
$min-width-lg: 900px;


$header-height-sm: 47px;
$header-height-md: 65px;
$header-height-lg: 70px;


/*  COLORS */
$color-green: rgba(61, 101, 86, .9);
$color-green-hover: rgba(36, 57, 49, .9);
$color-green-dark: rgba(49, 76, 66, 0.8);
$color-green-light: rgba(36, 171, 125, .9);
$color-green-light-hover: rgba(45, 148, 107, .9);
$color-white: #fff;
$color-white-alpha: rgba(255, 255, 255, .8);
$color-grey: #2b2b2b;
$color-disabled: rgba(116, 119, 118, 0.9);
$color-black-alpha: rgba(0, 0, 0, .2);
$color-red: #cd5f57;
$color-orange: #ffab56;
$color-blue: #64a5a9;

$color-black-bg: rgba(0, 0, 0, .8);

$color-invalid: rgba(255, 0, 11, .8);

$color-yellow: #fff17d;

$color-ava-green: #52be5d;
$color-ava-red: #ea5f56;
$color-ava-yellow: #eecf7e;

$color-red-state: #cc2128;
$color-green-state: #6cbb2d;






.page {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  transition: opacity 0.2s ease;
  overflow: hidden;
  &.bg-blur {
    background: url(~publicImg/bg-blur.jpg) center;
    background-size: cover;
  }
  .scroll-content {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    transform: translate3d(0,0,0);
  }
  &.show {
    opacity: 1;
    transition: opacity 0.5s ease;
  }
  .loading-block {
    position: absolute;
    background: rgba(0, 0, 0, 0.9);
    top: 0 - $header-height-sm;
    left: 200%;
    bottom: 0;
    width: 100%;
    opacity: 0;
    transition: opacity 0.2s ease;
    @media (min-width: $min-width-md) {
      top: 0 - $header-height-md;
    }
    @media (min-width: $min-width-lg) {
      top: 0 - $header-height-lg;
    }
    .loading-icon {
      position: absolute;
      background: url(~publicImg/loading.png);
      width: 112px;
      height: 112px;
      background-size: 100% 100%;
      transform-origin: 50% 50%;
      top: 50%;
      left: 50%;
      margin-top: -56px;
      margin-left: -56px;
      transform: rotate(0deg);
    }
    .text-block {
      position: absolute;
      top: 50%;
      margin-top: 70px;
      text-align: center;
      width: 100%;
    }
  }
  &.load {
    .loading-block {
      left: 0;
      opacity: 1;
      transition: opacity 1s ease 0.3s;
    }
    .loading-icon {
      transition: transform 60s linear;
      transform: rotate(10360deg);
    }
  }

}



