$general-font: 'Open Sans';

$min-width-md: 700px;
$min-width-lg: 900px;


$header-height-sm: 47px;
$header-height-md: 65px;
$header-height-lg: 70px;


/*  COLORS */
$color-green: rgba(61, 101, 86, .9);
$color-green-hover: rgba(36, 57, 49, .9);
$color-green-dark: rgba(49, 76, 66, 0.8);
$color-green-light: rgba(36, 171, 125, .9);
$color-green-light-hover: rgba(45, 148, 107, .9);
$color-white: #fff;
$color-white-alpha: rgba(255, 255, 255, .8);
$color-grey: #2b2b2b;
$color-disabled: rgba(116, 119, 118, 0.9);
$color-black-alpha: rgba(0, 0, 0, .2);
$color-red: #cd5f57;
$color-orange: #ffab56;
$color-blue: #64a5a9;

$color-black-bg: rgba(0, 0, 0, .8);

$color-invalid: rgba(255, 0, 11, .8);

$color-yellow: #fff17d;

$color-ava-green: #52be5d;
$color-ava-red: #ea5f56;
$color-ava-yellow: #eecf7e;

$color-red-state: #cc2128;
$color-green-state: #6cbb2d;






.modal-overflow {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  .modal-backdrop {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: black;
    opacity: 0;
    transition: opacity 0.3s ease;
  }
  .modal-scroll {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: auto;
  }
  .modal-body {
    background: $color-white;
    overflow: hidden;
    width: 290px;
    margin: 0 auto;
    padding: 20px;
    opacity: 0;
    transform: scale(0.8);
    transition: opacity 0.2s ease, transform 0.3s ease;
    color: black;
    border-radius: 10px;
  }
  &.show {
    .modal-backdrop {
      opacity: 0.5;
      transition: opacity 0.5s ease;
    }
    .modal-body, .modal-close {
      opacity: 1;
      transform: scale(1);
      transition: opacity 0.43s ease, transform 0.4s ease;
    }
  }
  .modal-close {
    position: absolute;
    top: 30px;
    right: 30px;
    font-size: 30px;
    opacity: 0;
    transform: scale(1);
    display: none;
  }
}
.full-modal {
  &.bg-blur {
    .modal-overflow {
      .modal-backdrop {
        background: url(~publicImg/bg-blur.jpg) center;
        background-size: cover;
      }
    }
  }
  .modal-close {
    display: block;
  }
  .modal-overflow {
    .modal-backdrop {
      background: url(~publicImg/bg.jpg) center;
      background-size: cover;
    }
    &.show {
      .modal-backdrop {
        opacity: 1;
      }
    }
    .modal-body {
      background: none;
      width: 100%;
      padding: 20px 0;
    }
  }
}



