$general-font: 'Open Sans';

$min-width-md: 700px;
$min-width-lg: 900px;


$header-height-sm: 47px;
$header-height-md: 65px;
$header-height-lg: 70px;


/*  COLORS */
$color-green: rgba(61, 101, 86, .9);
$color-green-hover: rgba(36, 57, 49, .9);
$color-green-dark: rgba(49, 76, 66, 0.8);
$color-green-light: rgba(36, 171, 125, .9);
$color-green-light-hover: rgba(45, 148, 107, .9);
$color-white: #fff;
$color-white-alpha: rgba(255, 255, 255, .8);
$color-grey: #2b2b2b;
$color-disabled: rgba(116, 119, 118, 0.9);
$color-black-alpha: rgba(0, 0, 0, .2);
$color-red: #cd5f57;
$color-orange: #ffab56;
$color-blue: #64a5a9;

$color-black-bg: rgba(0, 0, 0, .8);

$color-invalid: rgba(255, 0, 11, .8);

$color-yellow: #fff17d;

$color-ava-green: #52be5d;
$color-ava-red: #ea5f56;
$color-ava-yellow: #eecf7e;

$color-red-state: #cc2128;
$color-green-state: #6cbb2d;






.tips-widget {
  .tips-overflow {
    width: 100%;
    overflow: hidden;
  }
  .tips-item {
    position: relative;
    width: 20%;
    float: left;
    height: 43px;
    margin-top: 10px;
  }
  .tips-icon {
    width: 40px;
    height: 40px;
    margin: 0 auto;
    border: 1px solid #95ff81;
    border-radius: 20px;
    background-image: url(~publicImg/tips/team-help.png);
    background-position: center;
    background-repeat: no-repeat;
    &.disabled {
      border-color: #ececec;
    }
    .counter {
      border: 1px solid $color-white;
      color: $color-white;
      position: absolute;
      background: $color-orange;
      top: -6px;
      right: 5px;
      border-radius: 9px;
      margin-top: 0;
      display: inline-block;
      height: 15px;
      width: auto;
      padding: 0 4px;
      font-size: 10px;
      line-height: 13px;
    }
  }
  .color-orange {
    margin: 12px auto 0 auto;
    width: 160px;
  }
  @media (min-width: $min-width-md) {
    .tips-item {
      height: 60px;
    }
    .tips-icon {
      width: 54px;
      height: 54px;
      border-radius: 28px;
      .counter {
        top: -6px;
        border-radius: 10px;
        height: 20px;
        padding: 0 6px;
        font-size: 13px;
        line-height: 18px;
      }
    }
  }
}



